SelectPanel.prototype = {
  onAddItem: function (item) {
    if(!item.selected && this.selectedItems.length >= this.selectedMax) return;
    if(!item.selected){
      this.selectedItems.push(item);
    }else{
      for(var i=0; i<this.selectedItems.length; i++){
        if(item.name == this.selectedItems[i].name){
          this.selectedItems.splice(i,1);
        }
      }
    }
    item.selected = !item.selected;
    console.log(this.selectedItems);
  },

  toActiveType: function (idx) {
    if(this.selectedType){
      this.selectedType.selected = false;
      this.selectedDefaultRange.selected = false;
    }

    this.selectedType = this.dateTypes[idx];
    this.selectedType.selected = true;
    this.selectedDefaultRange = this.selectedType.defaultRanges[0];
    this.selectedDefaultRange.selected = true;
  },

  toggleRangePicker: function (){
    this.range.isopen = !this.range.isopen;
  },

  onCustomDef: function(){
    this.selectedDefaultRange = {
      values: []
    };
    if(this.selectedType.rangeStart){
      this.selectedDefaultRange.values.push(this.$filter('date')(this.dtStart, 'yyyy-MM-dd'));
    }

    if(this.selectedType.rangeEnd){
      this.selectedDefaultRange.values.push(this.$filter('date')(this.dtEnd, 'yyyy-MM-dd'));
    }

    this.toggleRangePicker();
  }
};

angular
  .module('app')
  .component('sideBar', {
    templateUrl: 'app/components/SideBar.html',
    controller: SideBar,
    bindings: {
      navs: '<'
    }
  });

/** @ngInject */
function SideBar($location) {
  var pathArray = $location.$$path.split('/');
  pathArray.shift();
  for(var i=0; i<this.navs.length; i++){
    var navPath = this.navs[i].path.split('/');
    navPath.shift();
    if(this.navs[i].children){
      for(var j=0; j<this.navs[i].children.length; j++){
        var navPath_j = this.navs[i].children[j].path.split('/');
        navPath_j.shift();
        if(pathArray[2] == navPath_j[2]){
          this.navs[i].active = true;
          this.navs[i].children[j].active = true;
        }
      }
    }else{
      if(pathArray[2] == navPath[2]){
        this.navs[i].active = true;
      }
    }
  }
};

SideBar.prototype = {
  onClick: function(toNav, level, parentNav){
    if(!this.navs[toNav].enable) return;

    if(level == 1){
      if(this.navs[toNav].active) return;

      for(var i=0; i<this.navs.length; i++){
        if(i == toNav){
          this.navs[i].active = true;
          if(this.navs[i].children){
            this.navs[i].children[0].active = true;
          }
        }else{
          this.navs[i].active = false;
          if(this.navs[i].children){
            for(var j=0; j<this.navs[i].children.length; j++){
              this.navs[i].children[j].active = false;
            }
          }
        }
        
      }
    }else if(level == 2){
      if(this.navs[parentNav].active && this.navs[parentNav].children[toNav].active) return;
      if(!this.navs[parentNav].active){
        for(var i=0; i<this.navs.length; i++){
          if(i == parentNav){
            this.navs[i].active = true;
            if(this.navs[i].children){
              this.navs[i].children[toNav].active = true;
            }
          }else{
            this.navs[i].active = false;
            if(this.navs[i].children){
              for(var j=0; j<this.navs[i].children.length; j++){
                this.navs[i].children[j].active = false;
              }
            }
          }
        }
      }else{
        for(var i=0; i<this.navs[parentNav].children.length; i++){
          this.navs[parentNav].children[i].active = false;
        }
        this.navs[parentNav].children[toNav].active = true;
      }
    }
  }
};
