/** @ngInject */
function loginCtrl($rootScope, $state, todoService) {
  console.log('loginCtrl');
  this.name = userInfo.name;
  this.password = '1';
  this.code = '';

  this.onSubmit = function(){
    console.log('[loginCtrl] onSubmit');
    $rootScope.currentUser = userInfo;
    $state.go('app.home.watchlist');
  }
}
 
var userInfo = {
  name: '',
  id: 0,
  avatar: ''
};

function UserService() {
}

UserService.prototype = {
  login: function (username, password) {
    return todos.map(function (todo) {
      return todo.id === id ?
        Object.assign({}, todo, {completed: !todo.completed}) :
        todo;
    });
  },

  register: function (username, password) {
    return todos.filter(function (todo) {
      return todo.id !== id;
    });
  },

  editTodo: function (id, text, todos) {
    return todos.map(function (todo) {
      return todo.id === id ?
        Object.assign({}, todo, {text: text}) :
        todo;
    });
  },

  completeAll: function (todos) {
    var areAllMarked = todos.every(function (todo) {
      return todo.completed;
    });
    return todos.map(function (todo) {
      return Object.assign({}, todo, {completed: !areAllMarked});
    });
  },

  clearCompleted: function (todos) {
    return todos.filter(function (todo) {
      return todo.completed === false;
    });
  }
};

